.movie-card-template {
  background-color: white;
  max-height: 440px;
  max-width: 280px;
  border: none;
}
@media screen and (min-width: 768px) {
  .movie-card-template {
    max-height: 495px;
    max-width: 335px;
  }
}
@media screen and (min-width: 1280px) {
  .movie-card-template {
    max-height: 700px;
    max-width: 395px;
  }
}

.movie-image {
  display: flex;
  justify-content: center;
  align-items: center;
  object-fit: contain;
  height: 400px;
  width: 280px;
  border-radius: 5px;
  border: none;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .movie-image {
    height: 455px;
    width: 335px;
  }
}
@media screen and (min-width: 1280px) {
  .movie-image {
    height: 575px;
    width: 395px;
  }
}

.movie-info {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  padding-top: 10px;
}

.movie-name {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 2px;
  text-transform: uppercase;
}

.tags-grade-wrap {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.movie-tags-year {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  font-weight: 500;
  color: #ff6b08;
}

.movie-grade {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 16px;
  width: 36px;
  background-color: #ff6b08;
  border-radius: 4px;
  border: none;
  font-family: 'Roboto', sans-serif;
  font-size: 10px;
  font-weight: 400;
  color: white;
}
